import DdzBaseHandler from "./DdzBaseHandler";

export default class DdzRefreshHandPokerHandler extends DdzBaseHandler{
    protected execute(msg)
    {
        let localSeat = this.SitHelper.serverSToLocalN(msg._src);
        if(localSeat == 0)
        {
            this.context.refreshHandPokers(msg._para.cards)
        }
    }

    protected executeSync(msg){
        this.execute(msg);
    }
}